<template>
    <v-btn 
        depressed
        rounded
        @click="onClick"
        :class="btnStyle"
        class="my-0 btn"
        :height="heightSize"
        :width="widthSize"
    >
        <template v-if="state === 'finish' || state === 'next'">
            <div>
                <span class="color_white font_semi_bold">다음</span>
            </div>
        </template>
        <template v-else-if="state === 'recognizing'">
            <div>
                <img class="btn-img-soundinteraction" src="@/assets/images/button/i_soundinteraction.png"/>
            </div>
        </template>
        <template v-else-if="state === 'replay'">
            <div>
                <img class="btn-img-sound" src="@/assets/images/button/i_sound.png"/>
            </div>
        </template>
        <template v-else-if="state === 'start'">
            <div>
                <span class="color_primary_4 font_semi_bold">말씀해 주세요</span>
            </div>
        </template>
        <template v-else>
            <div>
                <span class="color_primary_4 font_semi_bold"></span>
            </div>
        </template>
    </v-btn>
</template>

<script>
export default {
    name: 'BasicBtn',
    props: {
        title: {
            type: String,
            default: ''
        },
        widthSize: {
            type: String,
            default: '106px'
        },
        heightSize: {
            type: String,
            default: '44px'
        },
        dimBtn: {
            type: Boolean,
            default: false
        },
        state: {
            type: String,
            default: ''
        }
    },
    computed: {
        btnStyle() {
            let color = ''
            if (this.state === 'start' || this.state === 'recognizing') {
                color = 'back-white'
            } else {
                color = 'back-blue'
            }

            if (this.dimBtn) {
                color = 'cover'
            } 
            return color
        }
    },
    methods: {
        onClick() {
            this.$emit("onClick")
        },
        
    }
}
</script>

<style lang="scss" scoped>
.btn{
    border-radius: 30px; 
    padding: 2px 16px;
    left: 50%;
    transform: translateX(-50%);
    &-img {
        &-soundinteraction {
            width: 72px;
            height: 32px;
        }
        &-sound {
            height: 32px;
            width: 32px;
        }
    }
}
.back{
    &-white {
        width: 100%;
        height: 100%;
        background: #ffffff !important;
        color: #4e63da;
        border: 1px solid #4e63da;
    }
    &-blue {
        width: 100%;
        height: 100%;
        background: #4e63da !important;
        color: #ffffff;
    }
}

.cover {
    background: #b8c0f0 !important;
    color: #ffffff;
}
.cover_dim {
    background: #b8c0f0;
    color: #ffffff;
}
</style>